@tailwind base;
@tailwind components;
@tailwind utilities;

@layer utilities {
  .clip {
    clip-path: polygon(0 9%, 100% 0, 100% 94%, 0 100%);
  }

  .basket {
    background: #fff url('/src/assets/shopping-cart-button.png') center/contain no-repeat;
  }

  .block {
    display: block;
  }
}

.my-node-enter {
  opacity: 0;
}

.my-node-enter-active {
  opacity: 1;
  transition: opacity 200ms;
}

.my-node-exit {
  opacity: 1;
}

.my-node-exit-active {
  opacity: 0;
  transition: opacity 200ms;
}

.show-popup-enter {
  opacity: 0;
  transform: translate(-50%, -200%);
}

.show-popup-enter-active {
  opacity: 1;
  transform: translate(-50%, -50%);
  transition: all 200ms;
}

.show-popup-exit {
  opacity: 1;
}

.show-popup-exit-active {}

.show-popup-enter-done {
  opacity: 1;
  transform: translate(-50%, -50%);
  transition: all 200ms;
}

.my-num-enter {
  opacity: 0;
  animation-name: shake;
  animation-duration: 1s;
  animation-fill-mode: both;
  box-shadow: 0px 0px 16px 7px rgba(255, 0, 0, 1)
}

.my-num-enter-active {
  opacity: 1;
  transition: opacity 200ms;
}

.my-num-exit {
  opacity: 1;
}

.my-num-exit-active {
  opacity: 1;
  transition: opacity 200ms;
}

@keyframes shake {

  0%,
  to {
    transform: translateZ(0)
  }

  10%,
  30%,
  50%,
  70%,
  90% {
    transform: translate3d(-10px, 0, 0)
  }

  20%,
  40%,
  60%,
  80% {
    transform: translate3d(10px, 0, 0)
  }
}

@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;600;700&display=swap');